























































/* eslint-disable */
import { Component, Vue } from 'vue-property-decorator';

import ForgotPassViewModel from '@/viewmodels/Account/ForgotPass';

@Component
export default class extends Vue {
   email = '';
   snackBar = false;
   wrongEmail: boolean = false;
   rules = [(v: object) => !!v || 'Required'];

   sendEmail() {
      const emailSent = new ForgotPassViewModel().sendEmail(this.email.trim());

      if (emailSent) {
         this.snackBar = true;
         this.wrongEmail = false;
      } else {
         this.wrongEmail = true;
      }
   }
}
